
import { getBlog, postBlog, updateBlog } from '@/apis/blog';
import { Blog, RequiredBlog } from '@/interfaces/blog/Blog';
import Vue from 'vue'
export default Vue.extend({
    data() {
        return {
            blog: {
                title: '',
                abstract: '',
                content: '',
                hasPosted: true,
                tags: [''],
                authors: [''],
            },
            raw_blog: {
                title: '',
                abstract: '',
                content: '',
                hasPosted: true,
                tags: [''],
                authors: [''],
            },
            loading: false,
        }
    },
    methods: {
        async post_blog() {
            const id = this.$route.params.id;
            this.loading = true;
            let res;
            if (id) {
                const blog = this.blog;
                const raw_blog = this.raw_blog;
                Object.keys(this.blog).forEach(v => {
                    if (blog[v] === raw_blog[v]) delete blog[v];
                })
                res = await updateBlog(id, blog, this.$store.getters.getAccessToken);
            } else {
                res = await postBlog(this.blog, this.$store.getters.getAccessToken);
            }
            this.$store.commit('showMessage', res);
            this.loading = false;
        }
    },
    async mounted() {
        const id = this.$route.params.id;
        if (id) {
            const res = await getBlog<Blog>({ id }, true, this.$store.getters.getAccessToken);
            if (res?.success) {
                const { title, abstract, content, hasPosted, tags, authors } = res.data as RequiredBlog;
                this.blog = { title, abstract, content, hasPosted, tags, authors };
                this.raw_blog = { title, abstract, content, hasPosted, tags, authors };
            } else this.$store.commit('showMessage', res);
        }
    }
})
